import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Modal, overlayConfigFactory } from 'angular2-modal';
import { BSModalContext } from 'angular2-modal/plugins/bootstrap';
import { BaseComponent } from 'app/base/base.component';
import { ConfirmpopupComponent } from 'common/confirmpopup/confirmpopup.component';
import { Constants } from 'core/constants';
import { HttpUserAuthUtilsService } from 'core/http_wrapper/http-userauth-utils.service';
import { HttpSuccesFailureResponse } from 'core/http_wrapper/http_wrapper_response';
import { LocalStorageService } from 'core/service/local-storage.service';

@Component({
  selector: 'app-coupons',
  templateUrl: './coupons.component.html',
  styleUrls: ['./coupons.component.css']
})
export class CouponsComponent extends BaseComponent implements OnInit,HttpSuccesFailureResponse {
  couponsData:any[]=[];
  listIsLaoding=false;
  headerTitleGYMRegular=[{name:"Sr No",view:false,id:0},{name:"Promo Code",view:false,id:0},{name:"Description",view:false,id:0},{name:"Duration",view:false,id:0},{name:"Packages",view:false,id:0},{name:"Redemptions",view:false,id:0},{name:"Gender",view:false,id:0},{name:"Action",view:false,id:0}]
  isMasterGym:boolean=false;
  gymId:any=-1;
  userType:any;
  gymList:any[]=[];
  isLoading:any=false;
  selectedStatus= true;
  itemToDelete:any;

  constructor(private userAuthHttpService:HttpUserAuthUtilsService,private route: ActivatedRoute,private router: Router,private modal: Modal) {
    super(userAuthHttpService);
   }

  ngOnInit() {
    this.loadAdminDataIfLoggenIn();
    this.userType = LocalStorageService.getUserType();
    this.isMasterGym = LocalStorageService.isMasterGym();
    if(this.isMasterGym){
      this._userAuthHttpService.getGymList(this,true);
    }
    else{
      this.gymId=LocalStorageService.getUserId();

    }
    this.getCouponList();

  }

  onSuccess(type: any, responsedata: any) {
    if(type==Constants.getCouponListApi){
      this.couponsData=[]
      responsedata.data.forEach(element => {
          
          this.couponsData.push(element);
        
        
      });
      this.isLoading = false;

    }
    if(type==Constants.editExistingPackageApi){
      // this.getCouponList();
    }
    if(type == Constants.deleteCouponApiCode){
      this.getCouponList();
    }
    if(type == Constants.gymListApi){
      this.gymList = responsedata.data;
    //   this.gymList = this.gymList.filter((element) => {
    //     return element.id != LocalStorageService.getMasterGymId();
    //  });

    }
   
  }
  onFailure(type: any, response: any) {
    if(type == Constants.getCouponListApi){
      this.isLoading = false;

    }
    if(type==Constants.editExistingPackageApi){
      
    }
  }

  selectGym(event){
  
    let seletedGym=event.target.value;
    this.gymId=seletedGym;
    this.getCouponList();

  }
  selecteStatus(event){
    this.selectedStatus = event.target.value;
    this.getCouponList();
    
  
  }

  getCouponList(){
    this.isLoading = true;
    this._userAuthHttpService.getAllCoupons(this.selectedStatus,this,true,this.gymId);
  
  }

  editCoupon(item){
    this.router.navigateByUrl("coupons/"+item.id);

  }
  deleteCoupon(item){
    this.itemToDelete = item;
    this.openconfirmpopup("Do you really want to remove "+item.name+" ?","Are you Sure?","Yes","No",this,"PROMO_DELETE")

  }
  addCoupon(){
    this.router.navigateByUrl("coupons/addCoupon");

  }

  openconfirmpopup(message, title,yesTitle,noTitle, parent?: any, errorCode?: any) {
    return this.modal.open(ConfirmpopupComponent, overlayConfigFactory({ message, title ,yesTitle,noTitle, parent, errorCode}, BSModalContext));

  }

  confirmAction(status){
    this._userAuthHttpService.deleteCouponCode(this.itemToDelete.id,this,true);



  }

}



