import { DatePipe } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { forEach } from '@angular/router/src/utils/collection';
import { Constants } from 'core/constants';
import { HttpUserAuthUtilsService } from 'core/http_wrapper/http-userauth-utils.service';
import { LocalStorageService } from 'core/service/local-storage.service';
declare var $: any;

@Component({
  selector: 'app-add-coupon',
  templateUrl: './add-coupon.component.html',
  styleUrls: ['./add-coupon.component.css']
})
export class AddCouponComponent implements OnInit {
   isAddingNewCoupon=false;
   couponId:any;
   addCouponForm:FormGroup;
   gymList:any[]=[];
   isMasterGym:any;
   packageData=[];
   headerTitleGYMRegular=[{name:"Package Name",view:false,id:0},{name:"Package Type",view:false,id:0},{name:"Amount(KD)",view:false,id:0},{name:"Discount",view:false,id:0},{name:"Discounted Price (KD)",view:false,id:0}]
   gymId:any;
   couponData:any;

  constructor(private _userAuthHttpService:HttpUserAuthUtilsService,private route: ActivatedRoute,private router: Router,public formBuilder: FormBuilder) { }

  ngOnInit() {
    this.isMasterGym = LocalStorageService.isMasterGym();

    if(this.isMasterGym){
      this._userAuthHttpService.getGymList(this,true);
    }else{
      this.gymId = LocalStorageService.getUserId();
    }
    this.getPackage();
    this.route.params.subscribe(params => {
     if(params['id']){
         this.couponId = params['id'] || null;
        if(this.couponId=='addCoupon'){
          this.isAddingNewCoupon=true;

        }else{
          this.isAddingNewCoupon=false;
          this.getPromoData();

        }

      }
    });
this.initForm();
  }

  initForm(){
    let that= this;
    this.addCouponForm = this.formBuilder.group({
      name: ['', [Validators.maxLength(600), Validators.required]],
      noOfUsers:['',[Validators.required]],
      description:['',[Validators.required]],
      gender:['',[Validators.required]],
      dateRange:[''],

      fromDate:['',[Validators.required]],
      toDate:['',[Validators.required]],
      gymId:[this.isMasterGym?'':this.gymId,[Validators.required]],
      packages: this.formBuilder.array([])
    });

    var daterangeIdentifier ="input[name=daterange]";

    $(function() {
      $(daterangeIdentifier).daterangepicker({ 
        autoUpdateInput: false,
        drops: "down",
        locale: {
            format: 'YYYY-MM-DD',
            cancelLabel: 'Close'
  
        },
         minDate:new Date(),
    },function(start, end, label) {
      $(daterangeIdentifier).val(start.format('YYYY-MM-DD') + ' - ' + end.format('YYYY-MM-DD'));
      that.patchfromandtodate(start.format('YYYY-MM-DD'),end.format('YYYY-MM-DD'));

    });
    }); 
  }

  getPromoData(){
    this._userAuthHttpService.getCouponById(this.couponId,this,true)
  }
  patchfromandtodate(from,to){
    this.addCouponForm.patchValue({fromDate:from,toDate:to});
 
  }

  initPackage(pkgItem) {
    let discount:any ='';
    let discounttype ='PERCENTAGE';
    let discountedPrice:any ='';
    let mappingId = null;

    try{
      this.couponData.promoCodePackageMappingList.forEach(element => {
        if(element.packageDetail.id == pkgItem.id){
          discount = element.value
          discounttype = element.discountType
          let amount = element.packageDetail.cost;
          mappingId = element.id;
          if(discounttype=='PERCENTAGE'&&discount){
            let discountValue:any = amount*(discount/100);
            if(discountValue>100){
              element.patchValue({discount:0});
               discount = element.get('discount').value;
               alert("Discount percentage can't be more than 100")
    
            }
            discountedPrice = amount-discountValue.toFixed(2);
           }else if(discounttype=='AMOUNT'&&discount){
            if(discount>amount){
              element.patchValue({discount:0});
               discount = element.get('discount').value;
               alert("Discount value can't be more than 100")
    
            }
            discountedPrice = amount-discount;

    
          }

        }
        
      });

    }catch(e){

    }

    return this.formBuilder.group({
      id: [pkgItem.id],
      mappingId:[mappingId],
      name: [pkgItem.name, Validators.required],
      type:[pkgItem.packageType,Validators.required],
      amount: [pkgItem.cost, Validators.required],
      discount: [discount],
      discounttype: [discounttype, Validators.required],
      discountedPrice: [discountedPrice]
    });

  }

  addPackage(pkgItem) {
    const control = <FormArray>this.addCouponForm.get('packages')
    control.push(this.initPackage(pkgItem))
  }
  getPackage(selectedGym?) {
    if(selectedGym){
      this.gymId = selectedGym.target.value;

    }
    if(this.gymId){
      this._userAuthHttpService.getAllMembershipPackages(this,true,this.gymId);

    }
       


}
  onSuccess(type: any, responsedata: any) {
   
    if(type == Constants.gymListApi){
      this.gymList = responsedata.data;
     

    }
    
    if(type == Constants.getCouponByIdApi){
      this.couponData = responsedata.data[0];
      this.gymId=this.couponData.gymId;
      var datePipe = new DatePipe("en");
      this.addCouponForm.patchValue({name:this.couponData.name,noOfUsers:this.couponData.numOfUsers,description:this.couponData.description,gender:this.couponData.gender,fromDate:datePipe.transform(this.couponData.fromDate, 'yyyy-MM-dd'),toDate:datePipe.transform(this.couponData.toDate, 'yyyy-MM-dd'),gymId:this.couponData.gymId,dateRange:datePipe.transform(this.couponData.fromDate, 'yyyy-MM-dd')+" - "+datePipe.transform(this.couponData.toDate, 'yyyy-MM-dd')})

      this.getPackage();


    }
    if(type == Constants.getPackageListApi){
      this.packageData = responsedata.data;
      const control = <FormArray>this.addCouponForm.get('packages')
      control.controls =[];

      this.packageData.forEach(element => {
        this.addPackage(element);
      });
    }
    if(type == Constants.addCouponApiCode){
      this.router.navigate(['coupons']);
      setTimeout(() => {
        if(this.isAddingNewCoupon){
          alert("Coupon added successfully")

        }else{
          alert("Coupon updated successfully")

        }

      }, 500);


    }
  }
  onFailure(type: any, response: any) {
    if(type == Constants.addCouponApiCode){
      this.router.navigate(['coupons']);
        alert(response)
    


    }
  }
  setUpCouponData(){

  }

  getCouponCode(){

  }
  onDiscountChange(event){
   this.calculateDiscount()
  }
  onDiscountTypeChange(){
    this.calculateDiscount()

  }

  calculateDiscount(){
    const control = <FormArray>this.addCouponForm.get('packages')
    control.controls.forEach(element => {
      let discount = element.get('discount').value;
      let amount = element.get('amount').value;
     if(element.get('discounttype').value=='PERCENTAGE'&&discount){
        let discountValue:any = amount*(discount/100);
        if(discountValue>100){
          element.patchValue({discount:0});
           discount = element.get('discount').value;
           alert("Discount percentage can't be more than 100")

        }
        element.patchValue({discountedPrice:amount-discountValue.toFixed(2)});
       }else if(element.get('discounttype').value=='AMOUNT'&&discount){
        if(discount>amount){
          element.patchValue({discount:0});
           discount = element.get('discount').value;
           alert("Discount value can't be more than 100")

        }
        element.patchValue({discountedPrice:amount-discount});

      }else{
        element.patchValue({discountedPrice:''});
 
      }
    });

  }


  createRequestForApi(){
   
    if(this.addCouponForm.valid){
      const control = <FormArray>this.addCouponForm.get('packages')
      let promoCodePackageMappingList = [];
      let deletedPromoCodePackageMappingIds = [];
control.controls.forEach(element => {
  if(element.get('discount').value){
    let pkgControl={"id":element.get('mappingId').value,
    "promoCodeId":this.couponId=='addCoupon'?null:this.couponId,
    "packageId":element.get('id').value,
    "discountType":element.get('discounttype').value,
    "value":element.get('discount').value}
    promoCodePackageMappingList.push(pkgControl)
  }
  if(!element.get('discount').value&&element.get('mappingId').value){
    deletedPromoCodePackageMappingIds.push(element.get('mappingId').value);
  }
  
  
});
let request = {"name":this.addCouponForm.get('name').value,"description":this.addCouponForm.get('description').value,
      "fromDate":this.addCouponForm.get('fromDate').value,
      "toDate":this.addCouponForm.get('toDate').value,
      "numOfUsers":this.addCouponForm.get('noOfUsers').value,
      "gender":this.addCouponForm.get('gender').value,
      "gymId":this.gymId,
      "promoCodePackageMappingList":promoCodePackageMappingList,
      "deletedPromoCodePackageMappingIds":deletedPromoCodePackageMappingIds,
      "status":true
      }
      if(!promoCodePackageMappingList||promoCodePackageMappingList.length==0){
        alert("Please map atleast one package");
        return
      }
      if(this.isAddingNewCoupon){
        this._userAuthHttpService.addCouponCode(request,this,true);

      }else{
        this._userAuthHttpService.updateCouponCode(this.couponId,request,this,true);

      }

    }else{
this.markFormGroupTouched(this.addCouponForm)
    }
  }
private markFormGroupTouched(formGroup: FormGroup) {
    try {
      (<any>Object).values(formGroup.controls).forEach(control => {
        control.markAsTouched();
        if (control.controls) {
          control.controls.forEach(c => this.markFormGroupTouched(c));
        }
      });
    } catch (ex) {
      // Code to handle exception
    }
  }
}



